import { lazy, useState } from "react";
import MiddleBlockContent from "../../content/MiddleBlockContent.json";
import AboutContent from "../../content/AboutContent.json";
import ProductContent from "../../content/ProductContent.json";
import ContactContent from "../../content/ContactContent.json";
import { Card, List, Carousel, Modal, Image } from "antd";
import CountUp from "react-countup";
import VisibilitySensor from "react-visibility-sensor";
import { SvgIcon } from "../../common/SvgIcon";
import { Fade } from "react-awesome-reveal";
import "./style.css";
import Typewriter from "typewriter-effect";
import { Row, Col } from "antd";
import ReactPlayer from "react-player";

import { LeftContentSection, Content, ContentWrapper } from "./styles";

const Contact = lazy(() => import("../../components/ContactForm"));
const MiddleBlock = lazy(() => import("../../components/MiddleBlock"));
const Container = lazy(() => import("../../common/Container"));
const ScrollToTop = lazy(() => import("../../common/ScrollToTop"));
const ContentBlock = lazy(() => import("../../components/ContentBlock"));
const PhotoAlbum = lazy(() => import("react-photo-album"));

const Home = () => {
  const numberList = [
    {
      title: "Bags Produced",
      end: 600,
      svg: "orders.svg",
    },
    {
      title: "Orders Delivered",
      end: 100,
      svg: "delivery.svg",
    },
    {
      title: "Customers",
      end: 10,
      svg: "customer.svg",
    },
  ];

  const [isModalOpen, setIsModalOpen] = useState(false);

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  const photosproduct = [
    { src: "img/image/n-1.png", width: 800, height: 600 },
    { src: "img/image/n-2.png", width: 1600, height: 900 },
    { src: "img/image/n-3.png", width: 1600, height: 900 },
    { src: "img/image/n-4.png", width: 1600, height: 900 },
    { src: "img/image/n-5.png", width: 1600, height: 900 },
    { src: "img/image/n-6.png", width: 1600, height: 900 },
    { src: "img/image/n-7.png", width: 1600, height: 900 },
    { src: "img/image/n-8.png", width: 1600, height: 900 },
    { src: "img/image/n-9.png", width: 1600, height: 900 },
    { src: "img/image/n-10.png", width: 1600, height: 900 },
    { src: "img/image/n-11.png", width: 1600, height: 900 },
    { src: "img/image/n-12.png", width: 1600, height: 900 },
    { src: "img/image/n-13.png", width: 1600, height: 900 },
    { src: "img/image/n-14.png", width: 1600, height: 900 },
    { src: "img/image/n-15.png", width: 1600, height: 900 },
  ];

  return (
    <div className="temp1">
      <div
        style={{
          color: "#FFFFFF",
          width: "100%",
          textAlign: "center",
          position: "absolute",
          marginTop: "15%",
          zIndex: "50",
          fontSize: "6vw",
        }}
      >
        GET CARRIED AWAY WITH OUR BAGS!
      </div>
      <video
        autoPlay
        muted
        loop
        playsInline
        id="homevideo"
        data-origwidth="0"
        data-origheight="0"
        style={{ width: "100%", display: "center" }}
      >
        <source src="img/vid/vrfibc_home.mp4" type="video/mp4" />
      </video>
      <Container>
        <ScrollToTop />
        <Fade delay={200}>
          <div
            style={{
              fontSize: "3vh",
              fontFamily: "NotoSans-Regular",
              color: "#18216d",
              borderColor: "#ff991a",
              padding: "3vw",
              marginTop: "10px",
              borderWidth: "thick",
              borderRadius: "30px",
              fontWeight: "bold",
            }}
            className="home-intro"
          >
            We are a leading FIBC bag company offering customized bulk bags for
            various industries with a focus on quality and customer service.
            <br />
            <Typewriter
              options={{
                strings: ["We know exactly what you need"],
                autoStart: true,
                loop: true,
                cursor: "!",
                cursorClassName: "typewritera",
                wrapperClassName: "typewritera",
              }}
            />
          </div>
        </Fade>
        <Fade delay={200}>
          <div
            style={{
              textAlign: "center",
              marginTop: "20px",
            }}
          >
            {" "}
            <h1>Your Product! Our Packaging!</h1>
            <PhotoAlbum
              layout="masonry"
              photos={photosproduct}
              padding={5}
              spacing={5}
            />
            {/* <Carousel autoplay>
              {photosproduct.map((i) => {
                return (
                  <div>
                    <Image height="300px" preview={true} src={i.src}>
                      <div style={{ marginTop: "10px" }}>{i.src}</div>
                    </Image>
                  </div>
                );
              })}
            </Carousel> */}
          </div>
        </Fade>
        <LeftContentSection>
          <Fade delay={200}>
            <h1>Who we are?!</h1>
            <Row justify="space-between" align="middle" id="about_home">
              <Col lg={11} md={11} sm={12} xs={24}>
                <ReactPlayer
                  url="https://www.youtube.com/watch?v=9g2OOUa9tjc"
                  height="30vh"
                  width="40vh"
                  controls={true}
                />
              </Col>
              <Col lg={11} md={11} sm={11} xs={24}>
                <ContentWrapper>
                  <h3>Our Customer-Centric Approach</h3>
                  <Content>
                    At VR FIBC Bags, we prioritize your needs above all else.
                    Our commitment to providing tailored solutions means we
                    never resort to aggressive sales tactics. Instead, we offer
                    honest advice and guidance that aligns with your specific
                    requirements. Our dedication to customer satisfaction
                    extends beyond mere product supply – we can also facilitate
                    delivery and collection of bulk bags, alongside training
                    your staff in proper handling procedures.
                  </Content>
                </ContentWrapper>
              </Col>
            </Row>
          </Fade>
        </LeftContentSection>
        <LeftContentSection>
          <Fade delay={200}>
            <Row justify="space-between" align="middle" id="about_home">
              <Col lg={11} md={11} sm={11} xs={24}>
                <ContentWrapper>
                  <h3>Personalized Solutions for Every Client</h3>
                  <Content>
                    We take pride in our ability to deliver personalized and
                    bespoke services to all our clients, regardless of their
                    size or needs. Whether you're a small-scale operation or a
                    large enterprise, our approach remains consistent: we cater
                    to your unique demands. Our expertise in bulk bags and
                    packaging solutions ensures that you receive the perfect fit
                    for your business.
                  </Content>
                </ContentWrapper>
              </Col>
              <Col lg={11} md={11} sm={12} xs={24}>
                <Image src="img/image/1-nmin.jpeg" width="800" height="600" />
              </Col>
            </Row>
          </Fade>
        </LeftContentSection>
        <LeftContentSection>
          <Fade delay={200}>
            <Row justify="space-between" align="middle" id="about_home">
              <Col lg={11} md={11} sm={12} xs={24}>
                <Image src="img/image/2-nmin.jpeg" width="800" height="600" />
              </Col>
              <Col lg={11} md={11} sm={11} xs={24}>
                <ContentWrapper>
                  <h3>Unmatched Sourcing and Quoting</h3>
                  <Content>
                    Struggling to find the exact product you're looking for?
                    Look no further. VR FIBC JUMBO BAG INDUSTRIES is here to
                    help. Our extensive network and industry knowledge enable us
                    to source even the most specific products to meet your
                    requirements. If you're seeking a reliable supplier for bulk
                    bags, reach out to us for a quote. With VR FIBC JUMBO BAG
                    INDUSTRIES, your search ends here.
                  </Content>
                </ContentWrapper>
              </Col>
            </Row>
          </Fade>
        </LeftContentSection>
        <MiddleBlock
          title={MiddleBlockContent.title}
          content={MiddleBlockContent.text}
          button={MiddleBlockContent.button}
        />

        <Fade delay={200}>
          <h1>What clients says</h1>
          <Carousel autoplay dots={true}>
            <div>
              <div style={{ display: "flex", justifyContent: "center" }}>
                <Card
                  bordered={true}
                  hoverable={false}
                  style={{
                    textAlign: "center",
                    width: "300px",
                    height: "400px",
                    fontSize: "15px",
                    fontFamily: "NotoSans-Bold",
                    color: "#18216d",
                    borderRadius: "12px",
                    alignItems: "center",
                    borderColor: "#18216d",
                    display: "flex",
                    justifyContent: "center",
                  }}
                >
                  "I highly recommend VRFIBC for FIBC products. Their bags are
                  made from high-quality materials and are user-friendly. The
                  bags are durable and can withstand transportation and storage.
                  Their customer service team is also responsive and helpful
                  throughout the purchasing process".
                  <br />- Mr. Noam (Israel)
                </Card>
              </div>
            </div>
            <div>
              <div style={{ display: "flex", justifyContent: "center" }}>
                <Card
                  bordered={true}
                  hoverable={false}
                  style={{
                    textAlign: "center",
                    width: "300px",
                    height: "400px",
                    fontSize: "15px",
                    fontFamily: "NotoSans-Bold",
                    color: "#18216d",
                    borderRadius: "12px",
                    alignItems: "center",
                    borderColor: "#18216d",
                    display: "flex",
                    justifyContent: "center",
                  }}
                >
                  "Highly recommended for FIBC products. Excellent quality and
                  customer service." <br />- Mr. Michel (USA)
                </Card>
              </div>
            </div>
            <div>
              <div style={{ display: "flex", justifyContent: "center" }}>
                <Card
                  bordered={true}
                  hoverable={false}
                  style={{
                    textAlign: "center",
                    width: "300px",
                    height: "400px",
                    fontSize: "15px",
                    fontFamily: "NotoSans-Bold",
                    color: "#18216d",
                    borderRadius: "12px",
                    alignItems: "center",
                    borderColor: "#18216d",
                    display: "flex",
                    justifyContent: "center",
                  }}
                >
                  "Top-notch FIBC products and excellent customer service from
                  VRFIBC. Highly recommended" <br /> -Mr. Yasmin
                </Card>
              </div>
            </div>
          </Carousel>
        </Fade>

        <Fade delay={200}>
          <h1>Till now</h1>
          <List
            grid={{
              gutter: 16,
              xs: 1,
              sm: 1,
              md: 3,
              lg: 3,
              xl: 3,
              xxl: 3,
            }}
            dataSource={numberList}
            renderItem={(item) => (
              <List.Item>
                <Card
                  title={
                    <div>
                      <SvgIcon
                        src={item.svg}
                        aria-label="homepage"
                        width="70px"
                        height="70px"
                      />
                      <div>{item.title}</div>
                    </div>
                  }
                  bordered={true}
                  hoverable={true}
                  headStyle={{
                    color: "#18216d",
                    fontWeight: "600",
                    height: "100",
                  }}
                  style={{
                    margin: "3%",
                    borderRadius: "5px",
                    width: "150",
                    textAlign: "center",
                    fontSize: "15px",
                    zIndex: "30",
                    backgroundColor: "white",
                    borderColor: "rgb(24 33 109 / 10%)",
                  }}
                >
                  <CountUp end={item.end} redraw={true} suffix="k+">
                    {({ countUpRef, start }) => (
                      <VisibilitySensor onChange={start} delayedCall>
                        <b ref={countUpRef} />
                      </VisibilitySensor>
                    )}
                  </CountUp>
                </Card>
              </List.Item>
            )}
          />
        </Fade>

        <Modal
          open={isModalOpen}
          onOk={handleOk}
          centered
          onCancel={handleCancel}
          footer={null}
          closable={true}
          style={{
            overflow: "scroll",
          }}
        >
          <Contact
            title={ContactContent.title}
            content={ContactContent.text}
            id="contact-form"
          />
        </Modal>
        <div
          style={{ position: "fixed", bottom: "5%", right: "5%", zIndex: 200 }}
          onClick={showModal}
        >
          <SvgIcon
            src={"message.svg"}
            aria-label="homepage-message"
            width="70px"
            height="70px"
          />
        </div>
      </Container>
    </div>
  );
};

export default Home;
